<script>
import Gallery from "./gallery.vue";
import AppAuth from "./auth.vue";
import AppAd from "./ads.vue";
import { Capacitor } from "@capacitor/core";
export default {
  components: {
    AppGallery: Gallery,
    AppAuth,
    AppAd,
  },
  mounted() {
    const banner = document.getElementById("banner");
    banner.setAttribute("style", this.getBackground());

    // const aosCdn = document.createElement("script");
    // aosCdn.src = "https://unpkg.com/aos@2.3.1/dist/aos.js";
    // const aos = document.createElement("script");
    // aos.innerHTML = `
    // AOS.init({
    //   });
    // `;
    // console.log(aosCdn);
    // document.head.appendChild(aosCdn);
    // document.head.appendChild(aos);
  },
  computed: {
    adminData() {
      return this.$store.getters.adminData;
    },
  },
  methods: {
    getBackground() {
      return `
      background: linear-gradient(90deg, #000, rgba(0, 0, 0, 0.637)),
        url("${this.adminData.image1}") fixed;
      background-size: cover;
      `;
    },
    getStarted() {
      document.getElementById("feature-first").scrollIntoView({ behavior: "smooth" });
    },
    gotoLink(link) {
      if (link === "whatsapp") {
        window.open(
          "https://api.whatsapp.com/send?phone=2" +
            this.adminData.whatsapp +
            "&submit=Continue",
          "_blank"
        );
      }

      if (link === "email") {
        window.open(
          "mailto:" +
            this.adminData.email +
            "?subject=Subject&body=message%20goes%20here",
          "_blank"
        );
      }

      if (link === "telegram") {
        window.open(this.adminData.telegram);
      }

      if (link === "facebook") {
        if (this.adminData.facebook.includes("messenger"))
          return window.open(this.adminData.facebook, "_blank");
        if (!navigator.platform.includes("Win32") || Capacitor.isNative) {
          window.open("fb://facewebmodal/f?href=" + this.adminData.facebook);
        } else {
          window.open(this.adminData.facebook, "_blank");
        }
      }
    },
  },
};
</script>
